  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Extra Small and up (Portrait Phones) */
.Bio{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 40% 45% 15%;
    padding-bottom: 0%;
    background-color: #FBF6F6;
    /* border-top: solid 50px #F9D1A0; */
}
#bioImage{
    display: none;
}
.bioImageBackground{
    display: none;
}
.icons{
    margin-top: 5%;
    margin-bottom: 10%;
    grid-column: 1 / 2;
    grid-row: 3 / 4;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    align-self: center;
}
.bioInfo{
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin-left: 10%;
    margin-top: 8%;
    margin-bottom: 0%;
    color: #2D2C2B;
}
#name{
    display: -webkit-flex;
    display: flex;
    color: #6D7172;
    /* text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080; */
    font-family: voltage, sans-serif;
    font-weight: 300;
    font-style: normal;
    font-size: 48px;
    margin-bottom: 5%;
}
#title{
    font-family: nobel, sans-serif;
    font-style: normal;
    font-weight: 100;
    font-size: 22px;
    margin-top: 3%;
    margin-bottom: 3%;
}
#stack{
    font-family: nobel, sans-serif;
    font-style: normal;
    font-weight: 100;
    font-size: 22px;
    margin-top: 3%;
}
.contactinfo{
    display: grid;
    grid-template-columns: 300px;
    grid-template-rows: 25% 25% 20% 20%;
    text-align: left;
    margin-left: 0px;
    margin-top: 10%;
    margin-bottom: 0%;
