h2 > a {
    position: relative;
    color: #273D51;
    text-decoration: none;
}
  
h2 > a:hover {
    color: #019098;
}
  
h2 > a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: .5px;
    bottom: 0;
    left: 0;
    background-color: #019098;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}
h2 > a:hover:before{
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}
  
